ile transitioning from sidebar mini to full sidebar\r\n  .nav-sidebar,\r\n  .nav-sidebar > .nav-header,\r\n  .nav-sidebar .nav-link {\r\n    white-space: nowrap;\r\n    overflow: hidden;\r\n  }\r\n\r\n  // When the sidebar is collapsed...\r\n  &.sidebar-collapse {\r\n    .d-hidden-mini {\r\n      display: none;\r\n    }\r\n\r\n    // Apply the new margins to the main content and footer\r\n    .content-wrapper,\r\n    .main-footer,\r\n    .main-header {\r\n      margin-left: $sidebar-mini-width !important;\r\n    }\r\n\r\n    // Make the sidebar headers\r\n    .nav-sidebar .nav-header {\r\n      display: none;\r\n    }\r\n\r\n    .nav-sidebar .nav-link p {\r\n      width: 0;\r\n    }\r\n\r\n    .sidebar .user-panel > .info,\r\n    .nav-sidebar .nav-link p,\r\n    .brand-text {\r\n      margin-left: -10px;\r\n      animation-name: fadeOut;\r\n      animation-duration: $transition-speed;\r\n      animation-fill-mode: both;\r\n      visibility: hidden;\r\n    }\r\n\r\n    .logo-xl {\r\n      animation-name: fadeOut;\r\n      animation-duration: $transition-speed;\r\n      animation-fill-mode: both;\r\n      visibility: hidden;\r\n    }\r\n\r\n    .logo-xs {\r\n      display: inline-block;\r\n      animation-name: fadeIn;\r\n      animation-duration: $transition-speed;\r\n      animation-fill-mode: both;\r\n      visibility: visible;\r\n    }\r\n\r\n    // Modify the sidebar to shrink instead of disappearing\r\n    .main-sidebar {\r\n      overflow-x: hidden;\r\n\r\n      &,\r\n      &::before {\r\n        // Don't go away! Just shrink\r\n        margin-left: 0;\r\n        width: $sidebar-mini-width;\r\n      }\r\n\r\n      .user-panel {\r\n        .image {\r\n          float: none;\r\n        }\r\n      }\r\n\r\n      &:hover,\r\n      &.sidebar-focused {\r\n        width: $sidebar-width;\r\n\r\n        .brand-link {\r\n          width: $sidebar-width;\r\n        }\r\n\r\n        .user-panel {\r\n          text-align: left;\r\n\r\n          .image {\r\n            float