-right: 0;\r\n    padding-left: 0;\r\n    padding-right: 0;\r\n  }\r\n}\r\n\r\n.list-header {\r\n  color: $gray-600;\r\n  font-size: 15px;\r\n  font-weight: bold;\r\n  padding: 10px 4px;\r\n}\r\n\r\n.list-seperator {\r\n  background: $card-border-color;\r\n  height: 1px;\r\n  margin: 15px 0 9px;\r\n}\r\n\r\n.list-link {\r\n  > a {\r\n    color: $gray-600;\r\n    padding: 4px;\r\n\r\n    &:hover {\r\n      color: $gray-900;\r\n    }\r\n  }\r\n}\r\n\r\n// User block\r\n.user-block {\r\n  float: left;\r\n\r\n  img {\r\n    float: left;\r\n    height: 40px;\r\n    width: 40px;\r\n  }\r\n\r\n  .username,\r\n  .description,\r\n  .comment {\r\n    display: block;\r\n    margin-left: 50px;\r\n  }\r\n\r\n  .username {\r\n    font-size: 16px;\r\n    font-weight: 600;\r\n    margin-top: -1px;\r\n  }\r\n\r\n  .description {\r\n    color: $gray-600;\r\n    font-size: 13px;\r\n    margin-top: -3px;\r\n  }\r\n\r\n  &.user-block-sm {\r\n    img {\r\n      width: $img-size-sm;\r\n      height: $img-size-sm;\r\n    }\r\n\r\n    .username,\r\n    .description,\r\n    .comment {\r\n      margin-left: 40px;\r\n    }\r\n\r\n    .username {\r\n      font-size: 14px;\r\n    }\r\n  }\r\n}\r\n\r\n// Image sizes\r\n.img-sm,\r\n.img-md,\r\n.img-lg {\r\n  float: left;\r\n}\r\n\r\n.img-sm {\r\n  height: $img-size-sm;\r\n  width: $img-size-sm;\r\n\r\n  + .img-push {\r\n    margin-left: $img-size-sm + $img-size-push;\r\n  }\r\n}\r\n\r\n.img-md {\r\n  width: $img-size-md;\r\n  height: $img-size-md;\r\n\r\n  + .img-push {\r\n    margin-left: $img-size-md + $img-size-push;\r\n  }\r\n}\r\n\r\n.img-lg {\r\n  width: $img-size-lg;\r\n  height: $img-size-lg;\r\n\r\n  + .img-push {\r\n    margin-left: $img-size-lg + $img-size-push;\r\n  }\r\n}\r\n\r\n// Image bordered\r\n.img-bordered {\r\n  border: 3px solid $gray-500;\r\n  padding: 3px;\r\n}\r\n\r\n.img-bordered-sm {\r\n  border: 2px solid $gray-500;\r\n  padding: 2px;\r\n}\r\n\r\n// Rounded and Circle Images\r\n.img-rounded {\r\n  @include border-radius($border-radius)\r\n}\r\n\r\n.img-circle {\r\n  @include border-radius(50%);\r\n}\r\n\r\n// Image sizes\r\n.img-size-64,\r\n.img-size-50,\r\n.img-size-32 {\r\n  height: auto;\r\n}\r\n\r\n.img-size-64 {\r\n  width: 64px;\r\n}\r\n\r\n.img-size-50 {\r\n  width: 50px;\r\n}\r\n\r\n.img-size-32 {\r\n  width: 32px;\r\n}\r\n\r\n// Block sizes\r\n.size-32,\r\n.size-40,\r\n.size-50 {\r\n  display: block;\r\n  text-align: center;\r\n}\r\n\r\n.size-32 {\r\n  height: 32px;\r\n  line-height: 32px;\r\n  width: 32px;\r\n}\r\n\r\n.size-40 {\r\n  height: 40px;\r\n  line-height: 40px;\r\n  width: 40px;\r\n}\r\n\r\n.size-50 {\r\n  height: 50px;\r\n  line-height: 50px;\r\n  width: 50px;\r\n}\r\n\r\n// General attachemnt block\r\n.attachment-block {\r\n  background: $gray-100;\r\n  border: 1px solid $card-border-color;\r\n  margin-bottom: 10px;\r\n  padding: 5px;\r\n\r\n  .attachment-img {\r\n    float: left;\r\n    height: auto;\r\n    max-height: 100px;\r\n    max-width: 100px;\r\n  }\r\n\r\n  .attachment-pushed {\r\n    margin-left: 110px;\r\n  }\r\n\r\n  .attachment-heading {\r\n    margin: 0;\r\n  }\r\n\r\n  .attachment-text {\r\n    color: $gray-700;\r\n  }\r\n}\r\n\r\n// Overlays for Card, InfoBox & SmallBox\r\n.card,\r\n.overlay-wrapper,\r\n.info-box,\r\n.small-box {\r\n  // Box overlay for LOADING STATE effect\r\n  > .overlay,\r\n  > .loading-img {\r\n    height: 100%;\r\n    left: 0;\r\n    position: absolute;\r\n    top: 0;\r\n    width: 100%;\r\n  }\r\n\r\n  .overlay {\r\n    @include border-radius($border-radius);\r\n    align-items: center;\r\n    background: rgba($white, 0.7);\r\n    display: flex;\r\n    justify-content: center;\r\n    z-index: 50;\r\n\r\n    > .fa,\r\n    > .fas,\r\n    > .far,\r\n    > .fab,\r\n    > .glyphicon,\r\n    > .ion {\r\n      color: $gray-800;\r\n    }\r\n\r\n    &.dark {\r\n      background: rgba($black, 0.5);\r\n\r\n      > .fa,\r\n      > .fas,\r\n      > .far,\r\n      > .fab,\r\n      > .glyphicon,\r\n      > .ion {\r\n        color: $gray-400;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n.tab-pane {\r\n  // Box overlay for LOADING STATE effect on Tab Panels\r\n  > .overlay-wrapper {\r\n    position: relative;\r\n    > .overlay {\r\n      border-top-left-radius: 0;\r\n      border-top-right-radius: 0;\r\n      flex-direction: column;\r\n      margin-top: -$card-spacer-x;\r\n      margin-left: -$card-spacer-x;\r\n      height: calc(100% + 2 * #{$card-spacer-x});\r\n      width: calc(100% + 2 * #{$card-spacer-x});\r\n\r\n      &.dark {\r\n        color: $white;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n// Ribbon\r\n.ribbon-wrapper {\r\n  height: $ribbon-wrapper-size;\r\n  overflow: hidden;\r\n  position: absolute;\r\n  right: -2px;\r\n  top: -2px;\r\n  width: $ribbon-wrapper-size;\r\n  z-index: 10;\r\n\r\n  &.ribbon-lg {\r\n    height: $ribbon-lg-wrapper-size;\r\n    width: $ribbon-lg-wrapper-size;\r\n\r\n    .ribbon {\r\n      right: $ribbon-lg-right;\r\n      top: $ribbon-lg-top;\r\n      width: $ribbon-lg-width;\r\n    }\r\n  }\r\n\r\n  &.ribbon-xl {\r\n    height: $ribbon-xl-wrapper-size;\r\n    width: $ribbon-xl-wrapper-size;\r\n\r\n    .ribbon {\r\n      right: $ribbon-xl-right;\r\n      top: $ribbon-xl-top;\r\n      width: $ribbon-xl-width;\r\n    }\r\n  }\r\n\r\n  .ribbon {\r\n    box-shadow: 0 0 $ribbon-border-size rgba($black, .3);\r\n    font-size: $ribbon-font-size;\r\n    line-height: $ribbon-line-height;\r\n    padding: $ribbon-padding;\r\n    position: relative;\r\n    right: $ribbon-right;\r\n    text-align: center;\r\n    text-shadow: 0 -1px 0 rgba($black, .4);\r\n    text-transform: uppercase;\r\n    top: $ribbon-top;\r\n    transform: rotate(45deg);\r\n    width: $ribbon-width;\r\n\r\n    &::before,\r\n    &::after {\r\n      border-left: $ribbon-border-size solid transparent;\r\n      border-right: $ribbon-border-size solid transparent;\r\n      border-top: $ribbon-border-size solid #9e9e9e;\r\n      bottom: -$ribbon-border-size;\r\n      content: '';\r\n      position: absolute;\r\n    }\r\n\r\n    &::before {\r\n      left: 0;\r\n    }\r\n\r\n    &::after {\r\n      right: 0;\r\n    }\r\n  }\r\n}\r\n\r\n// Scroll To Top\r\n.back-to-top {\r\n  bottom: 1.25rem;\r\n  position: fixed;\r\n  right: 1.25rem;\r\n  z-index: $zindex-control-sidebar + 1;\r\n\r\n  &:focus {\r\n    box-shadow: none;\r\n  }\r\n}\r\n\r\n// Pre\r\npre {\r\n  padding: .75rem;\r\n}\r\n\r\n// Blockquotes styles\r\nblockquote {\r\n  background: $white;\r\n  border-left: .7rem solid $primary;\r\n  margin: 1.5em .7rem;\r\n  padding: 0.5em .7rem;\r\n\r\n  .box & {\r\n    background: $gray-200;\r\n  }\r\n\r\n  p:last-child {\r\n    margin-bottom: 0;\r\n  }\r\n\r\n  h1,\r\n  h2,\r\n  h3,\r\n  h4,\r\n  h5,\r\n  h6 {\r\n    color: $primary;\r\n    font-size: 1.25rem;\r\n    font-weight: 600;\r\n  }\r\n\r\n  @each $color, $value in $theme-colors {\r\n    &.quote-#{$color} {\r\n      border-color: $value;\r\n\r\n      h1,\r\n      h2,\r\n      h3,\r\n      h4,\r\n      h5,\r\n      h6 {\r\n        color: $value;\r\n      }\r\n    }\r\n  }\r\n\r\n  @each $color, $value in $colors {\r\n    &.quote-#{$color} {\r\n      border-color: $value;\r\n\r\n      h1,\r\n      h2,\r\n      h3,\r\n      h4,\r\n      h5,\r\n      h6 {\r\n        color: $value;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n\r\n// Tab Custom Content\r\n\r\n.tab-custom-content {\r\n  border-top: $nav-tabs-border-width solid $nav-tabs-border-color;\r\n  margin-top: .5rem;\r\n  padding-top: .5rem;\r\n}\r\n\r\n.nav + .tab-custom-content {\r\n  border-top: none;\r\n  border-bottom: $nav-tabs-border-width solid $nav-tabs-border-color;\r\n  margin-top: 0;\r\n  margin-bottom: .5rem;\r\n  padding-bottom: .5rem;\r\n}\r\n\r\n\r\n// Badge BTN Style\r\n.badge-btn {\r\n  border-radius: $button-border-radius-xs;\r\n  font-size: $button-font-size-xs;\r\n  font-weight: 400;\r\n  padding: $button-padding-y-xs*2 $button-padding-x-xs*2;\r\n}\r\n\r\n.badge-btn.badge-pill {\r\n  padding: .375rem .6rem;\r\n}\r\n","//\n// Misc: Print\n//\n\n@media print {\n  //Add to elements that you do not want to show when printing\n  .no-print {\n    display: none !important;\n  }\n\n  //Elements that we want to hide when printing\n  .main-sidebar,\n  .main-header,\n  .content-header {\n    @extend .no-print;\n  }\n\n  //This is the only element that should appear, so let's remove the margins\n  .content-wrapper,\n  .main-footer {\n    @include translate(0, 0);\n    margin-left: 0 !important;\n    min-height: 0 !important;\n  }\n\n  .layout-fixed .content-wrapper {\n    padding-top: 0 !important;\n  }\n\n  //Invoice printing\n  .invoice {\n    border: 0;\n    margin: 0;\n    padding: 0;\n    width: 100%;\n  }\n\n  .invoice-col {\n    float: left;\n    width: 33.3333333%;\n  }\n\n  //Make sure table content displays properly\n  .table-responsive {\n    overflow: auto;\n\n    > .table tr th,\n    > .table tr td {\n      white-space: normal !important;\n    }\n  }\n}\n","//\n// Component: Text\n//\n\n// text modification\n.text-bold {\n  &, &.table td, &.table th {\n    font-weight: 700;\n  }\n}\n\n.text-xs {\n  font-size: $font-size-xs !important;\n}\n\n.text-sm {\n  font-size: $font-size-sm !important;\n}\n\n.text-md {\n  font-size: $font-size-base !important;\n}\n\n.text-lg {\n  font-size: $font-size-lg !important;\n}\n\n.text-xl {\n  font-size: $font-size-xl !important;\n}\n\n// text color variations\n@each $name, $color in $colors {\n  .text-#{$name} {\n    color: #{$color} !important;\n  }\n}\n","//\n// Component: Elevation\n//\n\n.elevation-0 {\n  box-shadow: none !important;\n}\n\n// Background colors (colors)\n@each $name, $value in $elevations {\n  .elevation-#{$name} {\n    box-shadow: $value !important;\n  }\n}\n","//\n// Mixins: Backgrounds\n//\n\n// Background Variant\n@mixin background-variant($name, $color) {\n  .bg-#{$name} {\n    background-color: #{$color} !important;\n\n    &,\n    > a {\n      color: color-yiq($color) !important;\n    }\n\n    &.btn {\n      &:hover {\n        border-color: darken($color, 10%);\n        color: darken(color-yiq($color), 7.5%);\n      }\n\n      &:not(:disabled):not(.disabled):active,\n      &:not(:disabled):not(.disabled).active,\n      &:active,\n      &.active {\n        background-color: darken($color, 10%) !important;\n        border-color: darken($color, 12.5%);\n        color: color-yiq(darken($color, 10%));\n      }\n    }\n  }\n}\n\n// Background Gradient Variant\n@mixin background-gradient-variant($name, $color) {\n  .bg-gradient-#{$name} {\n    @include bg-gradient-variant('&', $color);\n    color: color-yiq($color);\n\n    &.btn {\n      &.disabled,\n      &:disabled,\n      &:not(:disabled):not(.disabled):active,\n      &:not(:disabled):not(.disabled).active,\n      .show > &.dropdown-toggle {\n        background-image: none !important;\n      }\n\n      &:hover {\n        @include bg-gradient-variant('&', darken($color, 7.5%));\n        border-color: darken($color, 10%);\n        color: darken(color-yiq($color), 7.5%);\n      }\n\n      &:not(:disabled):not(.disabled):active,\n      &:not(:disabled):not(.disabled).active,\n      &:active,\n      &.active {\n        @include bg-gradient-variant('&', darken($color, 10%));\n        border-color: darken($color, 12.5%);\n        color: color-yiq(darken($color, 10%));\n      }\n    }\n  }\n}\n","//\n// Misc: Colors\n//\n\n// Background colors (theme colors)\n@each $name, $color in $theme-colors {\n  @include background-variant($name, $color);\n}\n\n// Background colors (colors)\n@each $name, $color in $colors {\n  @include background-variant($name, $color);\n}\n\n.bg-gray {\n  background-color: $gray-500;\n  color: color-yiq($gray-500);\n}\n\n.bg-gray-light {\n  background-color: lighten($gray-200, 3%);\n  color: color-yiq(lighten($gray-200, 3%)) !important;\n}\n\n.bg-black {\n  background-color: $black;\n  color: color-yiq($black) !important;\n}\n\n.bg-white {\n  background-color: $white;\n  color: color-yiq($white) !important;\n}\n\n// Gradient Background colors (theme colors)\n@each $name, $color in $theme-colors {\n  @include background-gradient-variant($name, $color);\n}\n\n// Gradient Background colors (colors)\n@each $name, $color in $colors {\n  @include background-gradient-variant($name, $color);\n}\n\n// Backgrund Color Disabled\n[class^='bg-'].disabled {\n  opacity: .65;\n}\n\n// Text muted hover\na.text-muted:hover {\n  color: theme-color(primary) !important;\n}\n\n// Link Styles\n.link-muted {\n  color: darken($gray-500, 30%);\n\n  &:hover,\n  &:focus {\n    color: darken($gray-500, 40%);\n  }\n}\n\n.link-black {\n  color: $gray-600;\n\n  &:hover,\n  &:focus {\n    color: lighten($gray-500, 20%);\n  }\n}\n\n// Accent colors (theme colors)\n@each $name, $color in $theme-colors {\n  @include accent-variant($name, $color);\n}\n\n// Accent colors (colors)\n@each $name, $color in $colors {\n  @include accent-variant($name, $color);\n}\n\n// Accent button override fix\n[class*=\"accent-\"] {\n  @each $name, $color in $theme-colors {\n    a.btn-#{$name} {\n      color: color-yiq($color);\n    }\n  }\n}\n","//\n// Mixins: Accent\n//\n\n// Accent Variant\n@mixin accent-variant($name, $color) {\n  .accent-#{$name} {\n    $link-color: $color;\n    $link-hover-color: darken($color, 15%);\n    $pagination-active-bg: $color;\n    $pagination-active-border-color: $color;\n\n    .btn-link,\n    a:not(.dropdown-item):not(.btn-app):not(.nav-link):not(.brand-link):not(.page-link):not(.btn) {\n      color: $link-color;\n\n      @include hover {\n        color: $link-hover-color;\n      }\n    }\n\n    .dropdown-item {\n      &:active,\n      &.active {\n        background: $color;\n        color: color-yiq($color);\n      }\n    }\n\n    .custom-control-input:checked ~ .custom-control-label {\n      &::before {\n        background: $color;\n        border-color: darken($color, 20%);\n      }\n\n      &::after {\n        $newColor: color-yiq($color);\n        background-image: str-replace($custom-checkbox-indicator-icon-checked, str-replace(#{$custom-control-indicator-checked-color}, '#', '%23'), str-replace(#{$newColor}, '#', '%23'));\n      }\n    }\n\n    .form-control:focus:not(.is-invalid):not(.is-warning):not(.is-valid),\n    .custom-select:focus,\n    .custom-control-input:focus:not(:checked) ~ .custom-control-label::before,\n    .custom-file-input:focus ~ .custom-file-label {\n      border-color: lighten($color, 25%);\n    }\n    \n    .page-item {\n      .page-link {\n        color: $link-color;\n      }\n\n      &.active a,\n      &.active .page-link {\n        background-color: $pagination-active-bg;\n        border-color: $pagination-active-border-color;\n        color: $pagination-active-color;\n      }\n\n      &.disabled a,\n      &.disabled .page-link {\n        background-color: $pagination-disabled-bg;\n        border-color: $pagination-disabled-border-color;\n        color: $pagination-disabled-color;\n      }\n    }\n\n    [class*=\"sidebar-dark-\"] {\n      .sidebar {\n        a:not(.dropdown-item):not(.btn-app):not(.nav-link):not(.brand-link) {\n          color: $sidebar-dark-color;\n      \n          @include hover {\n            color: $sidebar-dark-hover-color;\n          }\n        }\n      }\n    }\n\n    [class*=\"sidebar-light-\"] {\n      .sidebar {\n        a:not(.dropdown-item):not(.btn-app):not(.nav-link):not(.brand-link) {\n          color: $sidebar-light-color;\n\n          @include hover {\n            color: $sidebar-light-hover-color;\n          }\n        }\n      }\n    }\n  }\n}\n\n","/*!\r\n * OverlayScrollbars\r\n * https://github.com/KingSora/OverlayScrollbars\r\n *\r\n * Version: 1.13.0\r\n *\r\n * Copyright KingSora | Rene Haas.\r\n * https://github.com/KingSora\r\n *\r\n * Released under the MIT license.\r\n * Date: 02.08.2020\r\n */\nhtml.os-html,html.os-html>.os-host{display:block;overflow:hidden;box-sizing:border-box;height:100%!important;width:100%!important;min-width:100%!important;min-height:100%!important;margin:0!important;position:absolute!important}html.os-ht